import {InputLabel, TextField} from "@mui/material";
import React from "react";
import {ChangeFilterValue} from "../types/methods.type";
import {FilterProperty} from "../types/searchFilters.type";
import {FormControl} from "./formControl.component";
import {DateSelect} from "./dateSelect.component";
import {VisualizationTypeFilterSelect} from "./visualizationTypeFilterSelect.component";
import {VisualizationType} from "../../searchPage/types/visualization.type";


const fc = (label:boolean|null,c:JSX.Element)=><FormControl>
    {label&&<InputLabel>Value</InputLabel>}
    {c}
</FormControl>

function onChange(changeFilterValue:ChangeFilterValue){
    return (e:any)=>changeFilterValue(e?.target?.value);
}

export function FilterValueInput({filterProperty,filterValue, changeFilterValue}:{filterProperty:FilterProperty|null, filterValue:string|null, changeFilterValue:ChangeFilterValue}) {
    if (!filterProperty) return fc(true,<TextField variant='standard'/>)
    switch (filterProperty){
        case FilterProperty.name:
        case FilterProperty.owner:
            return fc(!filterValue,<TextField variant="standard" value={filterValue} onChange={onChange(changeFilterValue)}/>)
        case FilterProperty.views:
            return fc(!filterValue,<TextField variant="standard" value={filterValue} type='number' onChange={onChange(changeFilterValue)}/>)
        case FilterProperty.lastViewed:
            return fc(false,<DateSelect value={filterValue} onChange={onChange(changeFilterValue)}/>)
        case FilterProperty.type:
            return fc(!filterValue,<VisualizationTypeFilterSelect visualizationType={filterValue as VisualizationType} changeVisualizationType={onChange(changeFilterValue)}/>)
    }
    return null;
}